<template>
  <div class="container">
    <el-dialog
      class=""
      :title="$t('IDC.dialogShortCode.dialogTitle') + ''"
      :visible.sync="searchVisible"
      width="800px"
      :close-on-click-modal="false"
      :before-close="cancel"
    >
      <div class="container-condition">
        <el-form
          size="small"
          class="add-form"
          ref="condition"
          :model="condition"
          :rules="conditionRules"
          label-position="top"
          @submit.prevent.native
        >
          <el-form-item :label="$t('IDC.dialogShortCode.shortCode') + ''" prop="shortCode">
            <el-input
              size="small"
              v-model.trim="condition.shortCode"
              @keyup.native.enter="() => {}"
              placeholder="Please enter"
              @focus="() => {}"
            />
          </el-form-item>
          <el-form-item :label="$t('IDC.dialogShortCode.organizationName') + ''" prop="organizationName">
            <el-input
              size="small"
              v-model.trim="condition.organizationName"
              @keyup.native.enter="() => {}"
              placeholder="Please enter"
              @focus="() => {}"
            />
          </el-form-item>
          <el-form-item :label="$t('IDC.dialogShortCode.status') + ''" prop="status">
            <el-select
              size="small"
              v-model.trim="condition.status"
              @keyup.native.enter="() => {}"
              placeholder="Please enter"
              @focus="() => {}"
            >
              <el-option v-for="(item, index) in status" :key="index" :label="item.label" :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
          <div class="condition-options">
            <el-button plain size="small" @click="reset">{{ $t('common.button.reset') }} </el-button>
            <el-button plain size="small" @click="search" type="primary">{{ $t('common.button.search') }} </el-button>
          </div>
        </el-form>
      </div>
      <div class="container-products">
        <table-page
          :columns="columns"
          :get-table-data="loadOrganizations"
          :is-check="false"
          :is-search="searching"
          :is-single-check="true"
          :pagination="pagination"
          :sort-arr="['organizationName', 'status']"
          class=""
          id-key="shortCode"
          id="table"
          size="small"
          :initializingLoading="initializingLoading"
        >
          <template v-slot:short-code="scope">
            <div class="short-code">
              <div class="short-code-radio">
                <el-checkbox
                  @change="onChange(scope.data.row, $event)"
                  v-if="isAllowCheck(scope.data.row.status)"
                  v-model="store[scope.data.row.shortCode]"
                >
                </el-checkbox>
                <el-checkbox
                  @change="onChange(scope.data.row, $event)"
                  disabled
                  v-else
                  v-model="store[scope.data.row.shortCode]"
                >
                </el-checkbox>
              </div>
              <div class="short-code-content">{{ scope.data.row.shortCode }}</div>
            </div>
          </template>
          <template v-slot:status="scope">
            <span class="circle"></span>{{ showStatus(scope.data.row.status) }}
          </template>
        </table-page>
      </div>
      <span slot="footer" class="dialog-footer">
        <div class="option-buttons">
          <el-button size="small" @click="confirm" type="primary">{{ $t('common.button.confirm') }} </el-button>
          <el-button plain size="small" @click="cancel">{{ $t('common.button.cancel') }}</el-button>
        </div>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import TablePage from '@/components/tablePage/index.vue';
import { queryOrgListByPage } from '@/views/identifier-center/api/api-organization';
import { STATUS_MAP } from '@/views/task-center/page/task-detail/utils/data-dic.js';
import {
  checkFuzzySearchInput,
  nonSpecialCharExceptPercentSignValidateObj,
  len24StrValidateObj,
} from '@/common/validator/common.js';

export default {
  name: 'PortalShortCode',
  components: { TablePage },
  mixins: {},
  props: {
    check: {
      type: Boolean,
      default: false,
    },
    default: {
      type: String,
      default: () => {
        return '';
      },
    },
    searchVisible: {
      type: Boolean,
      default: false,
    },
    shortCode: {
      type: String,
      default: () => {
        return '';
      },
    },
    initializingLoading: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      // checks: [],
      checkedKey: null,
      store: {},
      condition: {
        shortCode: '%',
        organizationName: '%',
        status: '',
      },
      conditionRules: {
        shortCode: [len24StrValidateObj],
        organizationName: [nonSpecialCharExceptPercentSignValidateObj],
      },
      searching: true,
      organizationName: '',
      status: [
        { label: 'All', value: '' },
        { label: 'Active', value: '03' },
        { label: 'Closed', value: '06' },
        { label: 'Dormant', value: '08' },
        { label: 'Frozen', value: '05' },
        { label: 'New', value: '00' },
        { label: 'Suspended', value: '04' },
      ],
    };
  },
  computed: {
    columns() {
      return [
        {
          label: this.$t('IDC.dialogShortCode.shortCode'),
          prop: 'shortCode',
          align: 'left',
          'sort-method': (a, b) => {
            return a.productName > b.productName ? 1 : -1;
          },
          __slotName: 'short-code',
        },
        {
          label: this.$t('IDC.dialogShortCode.organizationName'),
          prop: 'organizationName',
          align: 'left',
          'sort-method': (a, b) => {
            return a.productName > b.productName ? 1 : -1;
          },
        },
        {
          label: this.$t('IDC.dialogShortCode.status'),
          prop: 'status',
          align: 'left',
          __slotName: 'status',
        },
      ];
    },
    pagination() {
      return {
        pageIndex: 1,
        pageSize: 5,
        layout: 'total, prev, pager, next',
      };
    },
  },
  watch: {
    shortCode(value) {
      this.checkedKey = value;
      if (value) {
        this.store[this.checkedKey] = true;
      } else {
        for (const key in this.store) {
          this.store[key] = false;
        }
      }
    },
  },
  filter: {},
  created: function () {},
  activated: function () {},
  mounted: function () {},
  methods: {
    async loadOrganizations() {
      let data = {
        pageSize: this.pagination.pageSize,
        pageNo: this.pagination.pageIndex,
        shortCode: this.condition.shortCode,
        organizationName: this.condition.organizationName,
        status: this.condition.status,
      };
      let checkObj = {
        shortCode: data.shortCode,
        organizationName: data.organizationName,
        status: data.status,
      };
      // 未选择搜索条件时不进行接口调用
      if (!checkFuzzySearchInput(checkObj)) {
        this.$modal.msgError('Specify at least one search condition of Short Code, Organization Name and Status.');
        this.searching = false;
        this.searchLoading = false;
        return;
      }
      if (!data.shortCode.trim() && !data.organizationName.trim() && !data.status) {
        this.$modal.msgError(this.$t('common.tips.specifyAtLeastOne'));
        this.searching = false;
        this.searchLoading = false;
        return;
      }
      let res;
      try {
        res = await queryOrgListByPage(data);
        res.totalRecords = res.totalRecord;
        return res;
      } catch (e) {
        res = {
          header: { success: true },
          list: [],
          totalRecords: 0,
        };
      } finally {
        this.searching = false;
      }
      return res;
    },
    cancel() {
      this.closeDialog();
    },
    confirm() {
      this.$emit('update:shortCode', this.checkedKey);
      this.$emit('getOrgName', {});
      this.$emit('update:merchantName', this.organizationName);
      this.$emit('update:merchantId', this.merchantId);
      this.$emit('selected-data', this.checkedKey);
      this.$emit('submitShortCode');
      this.closeDialog();
    },
    closeDialog(reset = true) {
      if (reset) {
        this.reset(reset);
      }
      this.$emit('update:search-visible', false);
    },
    isAllowCheck(status) {
      return ['03', '04', '05'].includes(status);
    },
    onChange(data, value) {
      let key = data.shortCode;
      this.organizationName = data.organizationName || '';
      this.merchantId = data.organizationId || '';
      for (const _key in this.store) {
        if (_key === key) {
          this.store[_key] = value;
          this.checkedKey = value ? key : null;
        } else {
          this.store[_key] = false;
        }
      }
    },
    reset() {
      this.condition.shortCode = '%';
      this.condition.organizationName = '%';
      this.condition.status = '';
      this.pagination.pageIndex = 1;
    },
    search() {
      if (!this.initializingLoading) {
        this.searching = false;
      }
      this.$nextTick(() => {
        this.searching = true;
      });
    },
    showStatus(val) {
      return STATUS_MAP[val] || '-';
    },
  },
};
</script>

